body {
  margin: 0;
  padding: 0;
  color: #222;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  background: #f0f0f0;
  font-family: 'Ubuntu Mono Regular', monospace;
}

h1, h2, h3, h4 {
  padding: 0;
  font-weight: 700;
  margin: 0 0 15px 0;
}

h1 {
  font-size: 300%;
}

h2 {
  font-size: 210%;
}

h3 {
  font-size: 130%;
}

p {
  margin: 0 0 10px 0;
}

ul {
  margin: 0;
  padding: 0 0 0 10px;
  list-style-type: square;
}

a {
  color: #222;
}

.clear {
  clear: both;
}

#main {
  background: #fff;
  width: 900px;
  padding: 35px;
  margin: 0 auto 15px auto;
  overflow: hidden;

  box-shadow: 0 0 6px #ccc;
}

#title {
  line-height: 46px;
  border-bottom: 3px solid #222;
  padding-bottom: 30px;
  margin: 0px 0 30px 0;
}

#title .c {
  display: block;
}

#title .email {
  font-size: 20px;
  font-weight: 100;
  display: block;
  line-height: 30px;
}

#credit {
  margin-bottom: 20px;
  font-size: 11px;
  color: #999;
  text-align: center;
}

#credit a {
  color: #999;
}

p label {
  font-weight: 500;
  font-size: 110%;
  margin-right: 10px;
}

p label span.colon {
  display: inline;
}


.item {
  margin: 0 0 20px 0;
}

/*  _____________________________ */
.rtl {
  direction: rtl;
}

.photo .basic-info .set {
  float: none !important;
  width: auto !important;
}

.section.basic-info {
  border: 0;
}

.basic-info .address {
  width: 40%;
  float: left;
}

.basic-info .r {
  float: right;
  width: 58%;
  text-align: right;
}

.basic-info label {
  font-weight: 500;
}

.basic-info label,
.basic-info span {
  display: inline !important;
}

.basic-info p {
  margin: 0;
}

.section {
  margin-bottom: 10px;
}

.section:last-child {
  border: 0;
}

.section .title {
  float: left;
  font-size: 18px;
  line-height: 32px;
  font-weight: 500;
  word-wrap: break-word;
  width: 190px;
  margin-right: 30px;
  font-weight: 700;
}

.section .set {
  float: left;
  width: 68%;
}

.section .set h3 {
  margin: 0;
}

.section span.value {
  display: block;
}

.section h3 span.l,
.section span.r {
  width: 49%;
  float: left;
}

.section span.r {
  float: right;
  text-align: right;
}

.section-education .school,
.section-work .job_title {
  font-size: 100%;
  display: block;
  margin: 5px 0 5px 0;
}

/* ___ photo ___ */
#photo {
  width: 180px;
  height: 180px;
  overflow: hidden;
  border-radius: 3px;
  vertical-align: middle;
  position: absolute;
  border: 3px solid #222;
}

#photo img {
  width: 100%;
  height: auto;
  display: inline-block;
}

.header {
  margin-bottom: 40px;
}

.header.photo #title {
  margin-left: 220px;
}

.header.photo .basic-info {
  float: none;
  padding: 0;
  margin-left: 220px;
}

@media screen and (max-width: 950px) {
  body {
    background: #fff;
  }

  #main {
    box-shadow: none;
    padding: 0;
    padding: 5px;
  }
}